
body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.example {
  width: 100%;
  height: 100%;
}
.example .header {
    position: absolute;
    height: 32px;
    background-color: rgba(0, 0, 0, 0.1);
    left: 0;
    right: 0;
    top: 0;
    padding-left: 1em;
    line-height: 32px;
    overflow: scroll;
}
.example .vue-router-columns {
    position: absolute;
    top: 32px;
    left: 0;
    right: 0;
    bottom: 0px;
    height: auto !important;
}
.example .loader {
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    line-height: 100vh;
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.vue-router-columns .vue-router-column-view,
.vue-router-columns .vue-router-empty-column {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset -5px 0px 20px rgba(0, 0, 0, 0.05);
          box-shadow: inset -5px 0px 20px rgba(0, 0, 0, 0.05);
}
.vue-router-columns .vue-router-column-view::-webkit-scrollbar,
  .vue-router-columns .vue-router-empty-column::-webkit-scrollbar {
    display: none;
}
.lds-ring {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ring div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes lds-ring {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



